<template>
  <div>
    <p class="font-semibold my-6">
      Choose a shipping courier for your trade-ins
    </p>
    <div>
      <ShippingProvider
        @selected="handleSelectedCarrier"
        :carriers="carriers"
        :selected="selectedCarrier"
        :filters="carrierFilters"
        type="shipengine"
      ></ShippingProvider>
    </div>
  </div>
</template>
<script>
import { defineComponent } from 'vue'
import { useShippingProvider } from 'composable/integration/shipengine'

import ShippingProvider from 'components/Integration/ShippingProvider.vue'

export default defineComponent({
  components: {
    ShippingProvider
  },
  async setup() {
    const { carriers, carrierFilters, selectedCarrier, selectCarrier } =
      useShippingProvider()
    const handleSelectedCarrier = (selectedCarrier) => {
      selectCarrier(selectedCarrier)
    }

    return {
      carriers,
      carrierFilters,
      selectedCarrier,
      handleSelectedCarrier
    }
  }
})
</script>
